	module.exports = {"data":{"markdownRemark":{"html":"<h2>What options are available for PHP to connect to a database?</h2>\n<p>PHP can connect to a variety of different databases including MongoDB, MsSQL and MySQL.\nBoth PHP and MySQL are very popular and provide an easy, free and open source websites\nto be created and are often found together to produce websites of all types.\nBoth PHP and MySQL can scale to support large numbers of users.</p>\n<p>PHP even supports more than one way to deal with connections to MySQL including MySQLi Procedural,\nPHP Data Objects (PDO) and MySQLi Object Orientated along with the now deprecated MySQL Connect.\nWith PHP there are many features built into the core functionality of the language that make links to a\ndatabase simple and easy.</p>","fields":{"slug":"/php/working-with-databases/"},"frontmatter":{"title":"Working With Databases"}}},"pathContext":{"slug":"/php/working-with-databases/"}}

